const videoStageTime = [16, 10, , 8, 7, 10]; //16+10+8+7+10 = 51秒
//消息工厂,想把监听也放在这里,不知道对不对
export default class videosMsgFatory {
    static anlyArr() {
        let arr = [];
        for (let one of arguments[0]) {
            arr.push(new videosMsg(one[0], one[1]));
        }
        return arr;
    }
}

//单个视频消息
class videosMsg {
    type;//类型
    data;//数据
    constructor(type, data) {
        this.type = type;
        this.data = data;
    }
}